function toggleProducts() {
  let button = $('.products__button');
  let block = $('.products__container');
  for (let i = 0; i < Array.from(button).length; i++) {
    $(button[i]).on('click', function () {
      let id = button[i].getAttribute('data-id');
      let btn = $('.products__container[data-id="' + id + '"]');
      button.removeClass('active');
      $(this).addClass('active');
      block.removeClass('active');
      $(btn).attr('data-id', $(this).attr('data-id')).toggleClass('active');
    });
  }
}
toggleProducts();